	module.exports = {"data":{"site":{"siteMetadata":{"title":"Shang-Yu Su","subtitle":"PhD candidate at National Taiwan University, research interests cover Natural Language Processing, and Dialogue Systems.","copyright":"© All rights reserved.","author":{"name":"Shang-Yu Su","twitter":"#"},"disqusShortname":"","url":"https://www.shangyusu.com"}},"markdownRemark":{"id":"/Users/ShangYu/Desktop/github/shangyusu.github.io/src/pages/articles/AAAI2019-DSTC7-track3/index.md absPath of file >>> MarkdownRemark","html":"<p><b>This paper is published in the 7th Dialog System Technology Challenge (DSTC7) in the Proceedings of Thirty-Third AAAI Conference on Artificial Intelligence (AAAI 2019 - DSTC7).</b></p>\n<p>Full paper:\n<a href=\"/AAAI_2019___DSTC_Track_3-734c834eba9b64f63af0fae9f9030aba.pdf\" target=\"_blank\">Here</a></p>\n<p>Visual question answering and visual dialogue tasks have been increasingly studied in the multimodal field towards more practical real-world scenarios.\nA more challenging task, audio visual scene-aware dialogue (AVSD), is proposed to further advance the technologies that connect audio, vision, and language, which introduces temporal video information and dialogue interactions between a questioner and an answerer.\nThis paper proposes an intuitive mechanism that fuses features and attention in multiple stages in order to well integrate multimodal features, and the results demonstrate its capability in the experiments.\nAlso, we apply several state-of-the-art models in other tasks to the AVSD task, and further analyze their generalization across different tasks.</p>","fields":{"tagSlugs":["/tags/dialogue/","/tags/visual/"]},"frontmatter":{"title":"Reactive Multi-Stage Feature Fusion for Multimodal Dialogue Modeling","tags":["Dialogue","Visual"],"date":"2018-12-8T22:40:32.169Z","description":"Yi-Ting Yeh, Tzu-Chuan Lin, Hsiao-Hua Cheng, Yi-Hsuan Deng, Shang-Yu Su, and Yun-Nung Chen","conference":"DSTC7"}}},"pathContext":{"slug":"/posts/aaai2019-dstc7-avsd/"}}

